.using{
  width: 100%;
  float: left;
  position: relative;
  text-align: left;
  padding-top: 60px;
  box-sizing: border-box;
}

.using__wrap{
  width: 56%;
  float: left;
  h2{
    color: #98a7c4;
    font-family: $eb;
    font-size: 58px;
    font-weight: 700;
    line-height: 66px;
    width: 100%;
    margin: 0;
    text-align: left;
    margin-bottom: 70px;
    @include breakpoint(max-width 670px){
      font-size: 45px;
      margin-bottom: 40px;
    }
    @include breakpoint(max-width 770px){
        margin-bottom: 30px;
    }
  }
  @include breakpoint(max-width 670px){
    width: 100%;
  }
  @include breakpoint(max-width 770px){
    width: 100%;
  }
  @include breakpoint(min-width 1900px){
    width: 80%;
  }

}

.using__wrap_form{
  width: 100%;
  float: left;
  position: relative;
  background-color: #fff;
  padding: 55px;
  box-sizing: border-box;
  border-radius: 15px;
  box-shadow: 15.4px 14.3px 35px rgba(0, 0, 1, 0.16);
  h3{
    width: 100%;
    float: left;
    text-align: left;
    margin: 0;
    color: #0071f1;
    font-size: 40px;
    font-weight: 700;
    line-height: 24px;
    font-family: $eb;
    margin-bottom: 20px;
  }
  p{
    width: 100%;
    float: left;
    text-align: left;
    color: #98a7c4;
    font-size: 16px;
    font-weight: 500;
    font-family: $m;
    margin: 0;
  }
  span{
    width: 100%;
    float: left;
    text-align: left;
    color: #98a7c4;
    font-family: $eb;
    font-size: 20px;
    font-weight: 700;
    line-height: 24px;
    margin-top: 10px;
    display: inline-block;
  }
  a{
    margin-top: 10px;
    background-color: #0071f1;
    border-radius: 23px;
    box-shadow: 15.4px 14.3px 35px rgba(0, 0, 1, 0.34);
    float: left;
    color: #fff;
    text-align: center;
    display: inline-block;
    padding: 10px 35px;
    display: inline-block;
    box-sizing: border-box;
    margin-top: 20px;
    font-size: 15px;
    font-weight: 700;
    line-height: 24px;
    text-transform: uppercase;
    font-family: $eb;
    text-align: center;  -webkit-transform: scale(1,1);
    -webkit-transition-timing-function: ease-out;
    -webkit-transition-duration: 250ms;
    -moz-transform: scale(1,1);
    -moz-transition-timing-function: ease-out;
    -moz-transition-duration: 250ms;
    &:hover{
      -webkit-transform: scale(1.1,1.1);
       -webkit-transition-timing-function: ease-out;
       -webkit-transition-duration: 250ms;
       -moz-transform: scale(1.1,1.1);
       -moz-transition-timing-function: ease-out;
       -moz-transition-duration: 250ms;
    }
  }
  @include breakpoint(max-width 670px){
    padding: 15px;
    text-align: center;
    h3{
      font-size: 25px;
    }
    p{
      font-size: 20px;
      text-align: center;
    }
    span{
      font-size: 15px;
      text-align: center;
    }
    a{
      font-size: 18px;
      padding: 5px 20px;
      margin: 20px auto 0;
      display: inline-block;
      float: none;
    }
  }
}

.using-pic{
  position: absolute;
  right: 0;
  top: 59px;
  width: 710px;
  height: 600px;
  img{
    object-fit: contain;
    size: 100%;
  }
  @include breakpoint(max-width 670px){
    display: none;
  }
  @include breakpoint(max-width 770px){
    width: 240px;
    height: 283px;
    top: 11px;
  }
  @include breakpoint(min-width 1900px){
    top: 3px;
    width: 807px;
    height: 692px;
  }
}
