.header{
  width: 100%;
  float: left;
  position: relative;
  background-image: url("../img/header-fon.png");
  background-repeat: no-repeat;
  background-size: cover;
  height: 590px;
  // min-height: 892px;
  text-align: center;
  padding-top: 20px;
  box-sizing: border-box;
  @include breakpoint(max-width 670px){
    height: 685px;
    background-image: none;
    background-color: #4480e0;
  }
  @include breakpoint(max-width 770px){
    height: 685px;
    background-image: none;
    background-color: #4480e0;
  }
  @include breakpoint(min-width 1400px){
    min-height: 693px;
  }
  @include breakpoint(min-width 1500px){
    min-height: 775px;
  }
  @include breakpoint(min-width 1800px){
    min-height: 878px;
  }
  @include breakpoint(min-width 1900px){
    min-height: 892px;
  }

  // @include breakpoint(max-width 1370px){
  //   min-height: 590px;
  // }
}

.header__wrap{
  width: 64%;
  float: none;
  display: inline-block;
  margin: 0 auto;
  ul{
    width: 100%;
    float: left;
    margin: 0;
    padding: 0;
    display: block;
    li{
      margin:0 30px;
      float: left;
      text-align: center;
      display: inline-block;
      a{
        font-size: 18px;
        color: #fff;
        width: 100%;
        display: inline-block;
        float: left;
        font-family: $m;
        text-align: center;  -webkit-transform: scale(1,1);
        -webkit-transition-timing-function: ease-out;
        -webkit-transition-duration: 250ms;
        -moz-transform: scale(1,1);
        -moz-transition-timing-function: ease-out;
        -moz-transition-duration: 250ms;
        &:hover{
          -webkit-transform: scale(1.09,1.09);
           -webkit-transition-timing-function: ease-out;
           -webkit-transition-duration: 250ms;
           -moz-transform: scale(1.09,1.09);
           -moz-transition-timing-function: ease-out;
           -moz-transition-duration: 250ms;
        }
      }
    }
    @include breakpoint(max-width 670px){
      display: none;
      z-index: 10;
      li{
        width: 100%;
        text-align: center;
        margin: 0;
        a{
          font-size: 22px;
        }
      }
    }
  }
  p{
    width: 100%;
    float: left;
    display: inline-block;
    text-shadow: 0 0 24px rgba(0, 7, 6, 0.4);
    color: #fff;
    text-align: center;
    margin: 0;
    position: relative;
    font-family: $m;
    font-size: 48px;
    font-weight: 500;
    display: none;
    @include breakpoint(max-width 670px){
      font-size: 30px;
      line-height: normal;
      display: block;
    }
    @include breakpoint(max-width 770px){
      font-size: 35px;
      line-height: normal;
      display: block;
    }
  }
  @include breakpoint(max-width 670px){
    width: 100%;
  }
  @include breakpoint(max-width 770px){
    width: 100%;
  }
}

.header__logo{
  width: 100%;
  text-align: center;
  float: left;
  font-family: $ar;
  color: #fff;
  font-size: 40px;
  margin-bottom: 20px;
  @include breakpoint(max-width 670px){
    width: 85%;
    text-align: left;
    font-size: 40px;
    position: relative;
    top: 9px;
    padding-left: 20px;
    box-sizing: border-box;

  }
}

.header__title{
  width: 100%;
  float: left;
  position: relative;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  font-family: $eb;
  font-size: 100px;
  line-height: 110px;
  margin: 35px 0;
  display: none;
  @include breakpoint(max-width 770px){
    font-size: 70px;
    line-height: 60px;
    display: block;
    margin-bottom: 15px;
  }
  @include breakpoint(max-width 670px){
    font-size: 45px;
    line-height: 57px;
    display: block;
  }


}

.rocket-man{
  position: absolute;
  width: 321px;
  height: 541px;
  left: 28px;
  bottom: -21px;
  z-index: 8;
  img{}
  @include breakpoint(max-width 670px){
    display: none;
  }
  @include breakpoint(max-width 770px){
    display: none;
  }
}

.header__price{
  width: 100%;
  float: left;
  position: relative;
  text-align: center;
  display: none;
  @include breakpoint(max-width 770px){
    margin-top: 30px;
    display: block;
  }
  @include breakpoint(max-width 670px){
    margin-top: 30px;
    display: block;
  }

}

.header__price_item{
  width: 49%;
  float: left;
  position: relative;
  text-align: center;
  &:first-of-type{
    &:after{
      content: "=";
      display: inline-block;
      color: #fff;
      font-family: $rb;
      font-size: 113px;
      font-weight: 700;
      position: absolute;
      left: 90%;
      margin-top: -69px;
      @include breakpoint(max-width 770px){
        left: 85%;
        margin-top: -78px;

      }
      @include breakpoint(max-width 670px){
        font-size: 40px;
        margin-top: 37px;
        left: 48%;
      }
    }
  }
  .digits{
    width: 100%;
    float: left;
    text-align: center;
    font-family: $rb;
    font-size: 110px;
    font-weight: 700;
    text-transform: uppercase;
    color: #fff;
    line-height: 88px;
    @include breakpoint(max-width 770px){
      font-size: 80px;
      line-height: normal;

    }
    @include breakpoint(max-width 670px){
      font-size: 40px;
      line-height: normal;
    }

  }
  .follover, .rub{
    text-shadow: 2.2px 2px 5px rgba(0, 7, 6, 0.4);
    color: #fff;
    font-family: $rb;
    font-size: 40px;
    font-weight: 700;
    background-color: #ffd100;
    border-radius: 26px;
    display: inline-block;
    padding: 0px 32px;
    box-sizing: border-box;
    margin-top: 10px;
    line-height: 61px;
    @include breakpoint(max-width 670px){
      font-size: 25px;
      padding: 3px 20px;
      line-height: normal;
    }
  }
  .follover{
    background-color: #50baff;
  }
  @include breakpoint(max-width 770px){
    width: 50%;

  }
  @include breakpoint(max-width 670px){
    width: 100%;
    margin: 0 0 40px;
  }

}

.clouds-wrap{
  width: 100%;
  float: left;
  position: absolute;
  // height: 252px;
  left: 0;
  bottom: -3px;
  img{
    object-fit: contain;
    width: 100%;
  }
  @include breakpoint(max-width 670px){
    height: initial;
    bottom: -2px;
  }
  @include breakpoint(max-width 1950px){
    bottom: -5px;
  }
}

.toggle_mnu {
		display: none;
		    width: 40px;
		    height: 40px;
		    position: relative;
		    z-index: 3;
		    border: none;
		    right: 10px;
		    top: 20px;
		    background-color: transparent;
        outline: none;
        @include breakpoint(max-width 670px){
          display: block;
          float: right;
        }
       }
	.toggle_mnu	span {
    		display: block; }

			.sandwich {
			  width: 34px;
			  height: 34px;
			  position: absolute;
			  top: 10px;
			  bottom: 0;
			  left: 0;
			  right: 0;
			  margin: auto;
			  z-index: 200; }

				.sw-topper {
				  position: relative;
				  top: 0;
				  width: 34px;
				  height: 4px;
				  background: white;
				  border: none;
				  border-radius: 4px 4px 4px 4px;
				  transition: transform 0.5s, top 0.2s;
				  display: block; }

				.sw-bottom {
				  position: relative;
				  width: 34px;
				  height: 4px;
				  top: 6.6px;
				  background: white;
				  border: none;
				  border-radius: 4px 4px 4px 4px;
				  transition: transform 0.5s, top 0.2s;
				  transition-delay: 0.2s, 0; }

				.sw-footer {
				  position: relative;
				  width: 34px;
				  height: 4px;
				  top: 13.5px;
				  background: white;
				  border: none;
				  border-radius: 4px 4px 4px 4px;
				  transition: all 0.5s;
				  transition-delay: 0.1s; }

				.sandwich.active .sw-topper {
				  top: 9px;
				  transform: rotate(-45deg); }
				.sandwich.active .sw-bottom {
				  top: 6px;
				  transform: rotate(45deg); }
				.sandwich.active .sw-footer {
				  opacity: 0;
				  top: 0;
				  transform: rotate(180deg); }
