.conect{
  width: 100%;
  float: left;
  position: relative;
  padding: 60px 0;
  box-sizing: border-box;
  h2{
    color: #98a7c4;
    font-family: $eb;
    font-size: 58px;
    font-weight: 700;
    line-height: 66px;
    width: 100%;
    margin: 0;
    text-align: left;
    margin-bottom: 70px;
    @include breakpoint(max-width 670px){
      font-size: 45px;
      margin-bottom: 40px;
    }
    @include breakpoint(max-width 770px){
        margin-bottom: 30px;
    }
  }
}

.conect__right{
  width: 611px;
  float: right;
  background-image: url("../img/blue.png");
    height: 435px;
  background-repeat: no-repeat;
  position: absolute;
  right: 0;
  z-index: 1;
  padding:80px 40px;
  box-sizing: border-box;
  p{
    width: 80%;
    float: right;
    text-align: right;
    color: #fff;
    font-size: 20px;
    font-family: $db;
    line-height: 20px;
  }
  @include breakpoint(max-width 670px){
    width: 100%;
    position: relative;
    float: left;
    left: 0;
    padding: 10px;
    background-color: #0071f1;
    background-image: none;
    border-radius: 10px;
    margin-top: 10px;
    p{
      width: 100%;
      text-align: left;
    }
  }
  @include breakpoint(max-width 770px){
    position: relative;
    width: 100%;
    left: 0;
    margin-top: 30px;
    background-color: #0071f1;
    background-image: none;
    border-radius: 10px;
    height: auto;
    text-align: left;
    padding-top: 20px;
    padding-bottom: 20px;
    p{
      width: 100%;
    }
  }
}

.conect__left{
  width: 60%;
  float: left;
  position: relative;
  z-index: 20;
    border-radius: 10px;
    background: #FFF;
    right: 0;
    top: 22px;
    width: 702px;
    padding: 40px 30px;
    box-shadow: 0px 30px 30px 0px rgba(42, 85, 197, 0.1);
    @include breakpoint(max-width 670px){
      width: 100%;
      padding: 10px;
      top: 0;
    }
    @include breakpoint(max-width 770px){
      width: 100%;
      float: left;
    }
}

.conect__left_soc, .conect__left_payment{
  width: 100%;
  display: inline-block;
  float: left;
  span{
    width: 100%;
    float: left;
    text-align: left;
    font-size: 15px;
    float: left;
    color: #000;
    margin-bottom: 10px;
  }
  .item{
    width: 65px;
    float: left;
    margin-right: 15px;
    border: solid 2px #f0f4fc;
    border-radius: 100px;
    display: inline-block;
    height: 40px;
    text-align: center;
    cursor: pointer;
    i{
      line-height: 34px;
      &:nth-of-type(1){
        color: #2d76c7;
      }


    }
    img{
      size: 20px;
      position: relative;
      top: 7px;
      z-index: 20;
    }
    &:nth-of-type(2){
      i{
        color: #ff3829;
      }
    }
    &:nth-of-type(3){
      i{
        color: #8f1ebf;
      }
    }
    &:nth-of-type(4){
      i{
        color: #00b5fa;
      }
    }
  }
  .active{
    background-color: #4480f0;
    i{
      color: #fff;
    }
    &:nth-of-type(1){
      i{
        color: #fff
      }
    }
    &:nth-of-type(2){
      i{
        color: #fff
      }
    }
    &:nth-of-type(3){
      i{
        color: #fff
      }
    }
    &:nth-of-type(4){
      i{
        color: #fff
      }
    }
  }


}
.conect__left_payment{
  margin-top: 10px;
}
.conect__left_row{
  width: 100%;
  float: left;
  position: relative;
  margin-top: 10px;
  span{
    width: 100%;
    float: left;
    text-align: left;
    font-size: 15px;
    float: left;
    color: #000;
    margin-bottom: 10px;
  }
  input{
    width: 100%;
    border: solid 2px #f0f4fc;
    border-radius: 100px;
    height: 40px;
    float: left;
    padding-left: 15px;
    box-sizing: border-box;
    display: inline-block;
  }
}

.conect__left_wrap{
  width: 70%;
  float: left;
  position: relative;
  border-right: 1px solid #f0f4fc;
  padding-right: 50px;
  box-sizing: border-box;
  @include breakpoint(max-width 670px){
    width: 100%;
    border-right: none;
    padding-right: 0;
  }
}

.conect__left_info{
  width: 30%;
  float: left;
  padding-left: 10px;
  box-sizing: border-box;
  position: relative;
  p{
    text-align: center;
    font-size: 14px;
    color: #adbed9;
    font-weight: 300;
    margin-top: 108px;
    font-family: $l;
    position: absolute;
  }
  button{
    background: #f0f4fc;
    position: absolute;
    bottom: -300px;
    color: #98a7c4;
    display: inline-block;
    padding: 0 30px;
    height: 40px;
    line-height: 40px;
    outline: none;
    box-sizing: border-box;
    font-family: $l;
    text-align: center;
    border-radius: 100px;
    border: none;
  }
  @include breakpoint(max-width 670px){
    width: 100%;
    padding: 10px;
    top: 0;
    position: relative;
    text-align: center;
    p{
      position: relative;
      margin-top: 0;
    }
    button{
      position: relative;
      bottom: 0;
      margin: 0 auto;
      display: inline-block;
      float: none;
    }
  }
}
