.news-wrap{
  width: 100%;
  float: left;
  position: relative;
  padding-top: 70px;
  padding-bottom: 40px;
  box-sizing: border-box;
  @include breakpoint(max-width 670px){
    padding: 10px 0;
  }
}

.news-wrap__item{
  width: 27%;
  float: left;
  position: relative;
  // padding-right: 40px;
  box-sizing: border-box;
  margin-right: 9.5%;

  &:last-of-type{
    margin-right: 0;
  }
  span{
    width: 100%;
    float: left;
    text-align: left;
    color: #a8acb9;
    font-family: $db;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    text-transform: uppercase;
  }
  h4{
    width: 100%;
    float: left;
    text-align: left;
    color: #0071f1;
    font-family: $eb;
    font-size: 32px;
    font-weight: 700;
    line-height: 36px;
    display: inline-block;
  }
  p{
    width: 100%;
    float: left;
    position: relative;
    text-align: left;
    color: #4f5362;
    font-family: $db;
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
  }
    @include breakpoint(max-width 670px){
      width: 100%;
      margin-right: 0;
      margin-bottom: 20px;
      h4{
        font-size: 25px;
      }
    }
}
