.services{
  width: 100%;
  float: left;
  position: relative;
  text-align: center;
  padding-top: 50px;
  box-sizing: border-box;
}

.services__item{
  width: 33.3%;
  float: left;
  position: relative;
  span{
    width: 100%;
    float: left;
    text-align: center;
    position: relative;
    color: #0071f1;
    font-size: 30px;
    font-weight: 700;
    font-family: $eb;
  }
  p{
    width: 100%;
    float: left;
    text-align: center;
    color: #282828;
    font-size: 17px;
    font-weight: 500;
    line-height: 20px;
    font-family: $m;
    padding: 0 30px;
    box-sizing: border-box;
  }
  @include breakpoint(max-width 670px){
    width: 100%;
    float: left;
    margin-bottom: 15px;
    span{
      font-size: 30px;
    }
  }
}

.services__item_circle{
  width: 185px;
  height: 185px;
  background-color: #fff;
  display: inline-block;
  float: none;
  margin: 0 auto 20px;
  position: relative;
  border-radius: 50%;
  box-shadow: 0 0 27px rgba(0, 0, 1, 0.05);
  img{
    object-fit: contain;
    transform: translateY(50%);
  }
  &:before{
    content: "";
    width: 221px;
    height: 221px;
    opacity: 0.32;
    display: inline-block;
    border: 1px dashed #5492eb;
    position: absolute;
    border-radius: 50%;
    left: -19px;
    top: -19px;
  }
  @include breakpoint(max-width 670px){
    size: 150px;
    img{
      height: 73px;
    }
    &:before{
      content: "";
      width: 170px;
      height: 170px;
      opacity: 0.32;
      display: inline-block;
      border: 1px dashed #5492eb;
      position: absolute;
      border-radius: 50%;
      left: -11px;
      top: -11px;
    }
  }
}
