.adminka{
  width: 100%;
  float: left;
  position: relative;
  background-color: #f7f6f6;
}

.sidebar{
  background-image: linear-gradient(to top, #6dadfc 0%, #4480e0 100%);
  box-shadow: 1.5px 2.6px 15px rgba(0, 0, 0, 0.13);
  width: 300px;
  float: left;
  position: relative;
  height: 100vh;
  text-align: center;
  ul{
    width: 100%;
    padding: 0;
    margin: 0;
    float: left;
    display: block;
    text-align: left;
    li{
      width: 100%;
      display: inline-block;
      list-style-type: none;
      border-top: 1px solid rgba(241, 241, 241, 0.37);
      a{
        width: 100%;
        float: left;
        padding-left: 50px;
        box-sizing: border-box;
        padding-top: 15px;
        padding-bottom: 15px;
        position: relative;
        text-align: left;
        .thumb{
          float: left;
          border-radius: 50%;
          overflow: hidden;
          display: inline-block;
          size: 40px;
          position: relative;
          margin-right: 20px;
          img{
            object-fit: cover;
            size: 100%;
          }
        }
        span{
          color: #fff;
          position: relative;
          width: calc(100% - 60px);
          font-size: 15px;
          font-weight: 500;
          font-family: $m;
          position: relative;
          line-height: 38px;
          text-align: left;
          height: 40px;
        }
      }
    }
  }
  @include breakpoint(max-width 670px){
    width: 100%;
  }
}

.adminka-content{
  width: calc(100% - 300px);
  float: left;
  position: relative;
  @include breakpoint(max-width 670px){
    display: none;
  }
}

.sidebar__logo{
  width: 100%;
  text-align: center;
  float: left;
  font-family: $ar;
  color: #fff;
  font-size: 40px;
  color: #fff;
  padding: 22px 0;
  box-sizing: border-box;

}

.add-account{
  width: 211px;
  height: 46px;
  background-color: #f1f1f1;
  border-radius: 23px;
  box-shadow: 1.5px 2.6px 15px rgba(0, 0, 0, 0.29);
  float: none;
  margin: 20px auto;
  display: inline-block;
  color: #4581e0;
  font-family: $eb;
  text-align: center;
  font-size: 15px;
  font-weight: 700;
  line-height: 47px;
  text-transform: uppercase;
}

.adminka-content__panel{
  width: 100%;
  float: left;
  position: relative;
  background-color: #fff;
  padding: 25px;
  box-sizing: border-box;
  text-align: right;
  @include breakpoint(max-width 770px){
    padding-top: 14px;
    padding-bottom: 14px;
  }
  @include breakpoint(max-width 670px){
    padding: 10px;
  }
  p{
    display: inline-block;
    float: left;
    text-align: left;
    position: relative;
    color: #4481e0;
    font-family: $m;
    font-size: 16px;
    font-weight: 500;
    text-transform: uppercase;
    margin: 0;
    @include breakpoint(max-width 670px){
      font-size: 15px;
    }
  }
  span{
    display: inline-block;
    float: none;
    margin-right: 100px;
    @include breakpoint(max-width 770px){
      float: left;
      text-align: left;
    }
    @include breakpoint(max-width 670px){
      font-size: 12px;
    }
  }
  a{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 72px;
    height: 72px;
    background-color: #d6d1d8;
    span{
      background-image: url("../img/logout.png");
      width: 25px;
      height: 27px;
      display: block;
      position: relative;
      transform: translate(100%, 80%);
      @include breakpoint(max-width 670px){
      transform: translate(80%, 70%);
      background-size: 80%;
      background-repeat: no-repeat;
      }
    }
    @include breakpoint(max-width 670px){
      width: 57px;
      height: 57px;
    }
  }
}

.box-connent{
  width: 100%;
  float: left;
  position: relative;
  padding: 30px;
  box-sizing: border-box;
  overflow-y: scroll;
  height: 900px;
  @include breakpoint(max-width 670px){
    padding: 10px;
    height: 570px;
  }
}
.box-connent::-webkit-scrollbar-button {
  background-image:url('');
  background-repeat:no-repeat;
  width:5px;
  height:10px
}

.box-connent::-webkit-scrollbar-track {
background-color:#fff;
}

.box-connent::-webkit-scrollbar-thumb {
  -webkit-border-radius: 10px;
  border-radius: 5px;
  background-color:#6dadfc;
  height: 10px;
}

.box-connent::-webkit-scrollbar-thumb:hover{
  background-color:#6dadfc;
}

.box-connent::-webkit-resizer{
  background-image:url('');
  background-repeat:no-repeat;
  width:5px;
  height:0px
}

.box-connent::-webkit-scrollbar{
  width: 5px;
}
.page__tabcontent{
  width: 100%;
  float: left;
  box-sizing: border-box;
  background-color: #fff;
  box-shadow: 1.5px 2.6px 15px rgba(0, 0, 0, 0.13);
}

.one{
  display: block;
}
.two, .three, .four{
  display: none;
}

.page__tabcontent_table_row{
  width: 100%;
  float: left;
  margin-bottom: 43px;
  &:last-of-type{
    margin-bottom: 0;
  }
  .old, .new{
    width: 33.3%;
    float: left;
    position: relative;
    font-size: 16px;
    .title{
      width: 25.3%;
      float: left;
      position: relative;
      text-align: left;
      color: #4480e0;
      font-family: $m;
    }
    .time{
      width: 33.3%;
      text-align: center;
      float: left;
      position: relative;
      color: #6a6a6b;
      font-family: $m;
    }
    .count{
      float: left;
      color: #6a6a6b;
      font-family: $eb;
      min-width: 51px;
      height: 29px;
      background-color: #f7f6f6;
      border-radius: 15px;
      text-align: center;
      line-height: 28px;
      position: relative;
      top: -3px;
      padding: 0 10px;
      box-sizing: border-box;
      margin-left: 20px;
    }
      @include breakpoint(max-width 770px){
        width: 100%;
        margin-bottom: 10px;
      }
  }
  p{
    width: 30.3%;
    float: left;
    position: relative;
    text-align: right;
    color: #4480e0;
    font-family: $m;
    font-size: 16px;
    font-weight: 500;
      @include breakpoint(max-width 770px){
        width: 100%;
        float: left;
        text-align: left;
      }
  }
  .new{
    margin-left: 20px;
      @include breakpoint(max-width 770px){
        margin-left: 0;
      }
  }
  @include breakpoint(max-width 770px){
    border-bottom: 1px solid rgba(109, 173, 252, 0.37);
    margin: 0;
    padding: 15px 0;
    box-sizing: border-box;
    &:last-of-type{
      border-bottom: none;
    }
  }
}
.page__tabcontent_table{
  width: 100%;
  float: left;
  position: relative;
  padding: 24px 35px;
  box-sizing: border-box;
  @include breakpoint(max-width 670px){
    padding-top: 0;
    padding-bottom: 0;
  }
}

.sidebar-mob{
  width: 60px;
  .sidebar__logo{
    display: none;
  }
  .sidebar__logo-mob{
    display: block;
    width: 100%;
    float: left;
    text-align: center;
    padding: 15px 10px;
    box-sizing: border-box;
    cursor: pointer;
    span{
      width: 100%;
      background-color: #f1f1f1;
      height: 4px;
      border-radius: 25px;
      display: inline-block;
      float:left;
      margin-bottom: 7px;
      &:last-of-type{
        margin-bottom: 0;
      }
    }
  }
  ul{
      margin-top: 11px;
  }
  ul li a{
    padding: 45px 10px;
    text-align: center;
    .thumb{
      float: none;
      display: inline-block;
      margin: 0 auto;
    }
    span{
      display: none;
    }
  }
  .add-account{
    display: none;
  }
}

.adminka-content-mob{
  display: block;
  width: calc(100% - 60px);
}
