.footer{
  width: 100%;
  float: left;
  position: relative;
  background-image: url("../img/footer-fon.png");
  background-repeat: no-repeat;
  background-size: cover;
  height: 200px;
  text-align: center;
  box-sizing: border-box;
  padding: 46px 0;
  p{
    width: 100%;
    float: left;
    text-align: center;
    span,a{
      display: inline-block;
      float: none;
      position: relative;
      margin: 0 15px;
      font-size: 20px;
      color: #fff;
      font-family: $l;
    }
  }
}
.footer__logo{
  width: 100%;
  text-align: center;
  float: left;
  font-family: $ar;
  color: #fff;
  font-size: 40px;
  margin-bottom: 20px;
  @include breakpoint(max-width 670px){
    width: 100%;
    text-align: center;
    font-size: 30px;
  }
}

.modal_div{
	width: 450px;
	position: fixed;
	// top: 45%;
	// left: 50%;
	margin-top: calc(25% - (381px/2));
	margin-left: calc(50% - (450px/2));
	display: none;
	opacity: 0;
	z-index: 5;
  h2{
    width: 90%;
    float: left;
    position: relative;
    text-align: left;
    margin: 0;
    font-family: $eb;
    color: #fff;
    font-size: 42px;
    margin-bottom: 10px;
  }
  @include breakpoint(max-width 770px){
    margin-top: calc(50% - (381px/2));
  }

    @include breakpoint(max-width 670px){
      width: 100%;
      margin-top: 20%;
      margin-left: 0;
    }
}

.login-wrap{
  width: 100%;
  float: left;
  background-color: #fff;
  border-radius: 10px;
  padding: 25px;
  box-sizing: border-box;
  form{
    width: 100%;
    float: left;
    text-align: center;
  }
  p{
    width: 89%;
    float: none;
    display: inline-block;
    margin: 0 auto;
    font-family: $l;
    font-size: 16px;
    color: #98a7c4;
    line-height: 21px;
    text-align: center;
  }
}

.login-wrap__row{
  width: 100%;
  float: left;
  position: relative;
  margin-bottom: 15px;
  span{
    width: 100%;
    float: left;
    text-align: left;
    font-family: $l;
    color: #000;
    font-size: 17px;
    margin-bottom: 10px;
  }
  input{
    border: solid 2px #f0f4fc;
    border-radius: 25px;
    width: 100%;
    float: left;
    text-align: left;
    box-sizing: border-box;
    max-width: 100%;
    padding: 0 15px;
    height: 40px;
    outline: none;
  }
  button{
    background-color: #508bfa;
    color: #fff;
    float: left;
    font-family: $l;
    display: inline-block;
    padding: 0 30px;
    height: 40px;
    line-height: 40px;
    border: none;
    text-transform: uppercase;
    font-size: 14px;
    border-radius: 100px;
  }
  a{
    color: #4480f0;
    font-size: 17px;
    float: right;
    font-family: $l;
  }
}
.modal_close{
  width: 19px;
  height: 19px;
  position: absolute;
  top: 22px;
  right: 6px;
  cursor: pointer;
  display: block;
  font-size: 50px;
  color: #fff;
  &:after{
    content: "";
    display: inline-block;
    width: 18px;
    position: absolute;
    height: 3px;
    background-color: #9aa9c6;
    -webkit-transform: rotate(-45deg);
   -moz-transform: rotate(-45deg);
   -o-transform: rotate(-45deg);
  }
  &:before{
    content: "";
    display: inline-block;
    width: 18px;
    position: absolute;
    height: 3px;
    background-color: #9aa9c6;
    -webkit-transform: rotate(45deg);
   -moz-transform: rotate(45deg);
   -o-transform: rotate(45deg);

  }
}
/* Пoдлoжкa */
#overlay {
	z-index:3;
	position:fixed;
	background-color:#000;
	opacity:0.8;
	-moz-opacity:0.8;
	filter:alpha(opacity=80);
	width:100%;
	height:100%;
	top:0;
	left:0;
	cursor:pointer;
	display:none;
}
